<template>
  <div id="home">
    <div id="home-info-base">
      <div class="home-info">
        <img src="../assets/img/cat.jpg" />
        <div class="home-info-content">
          <p>小马甲奶糖</p>
        </div>
      </div>
      <div class="home-info">
        <span class="home-info-title">分类目录</span>
        <p>技术</p>
        <p>关于</p>
      </div>
      <div class="home-info">
        <span class="home-info-title">时光机</span>
        <p :key="value[0]" v-for="(value) in timeart">
          {{ value[0] }}  [{{ value.length }}]
        </p>
      </div>
    </div>
    <div id="home-content">
      <div class="home-content-preview" :key="preview.uuid" v-for="preview in previews">
        <div class="content-title">
          <router-link :to="'/content/' + preview.uuid" :key="key">{{preview.title}}</router-link>
        </div>
        <div class="home-content-preview-data" v-html="preview.data"/>
        <div class="home-content-preview-more"><span>{{ preview.timestr }}</span><router-link :to="'/content/' + preview.uuid" :key="key"> 阅读更多></router-link></div>
      </div>
    </div>
    <div id="home-other">
      <div class="content-other-item">
        <span class="home-info-title">最新</span>
        <p :key="preview.uuid" v-for="preview in previews">
          <router-link :to="'/content/' + preview.uuid" :title="preview.title" :key="key"> ·
            <template v-if="preview.title.length > 18"> {{ preview.title.substring(0, 15) + '...' }} </template>
            <template v-else> {{ preview.title }} </template>
          </router-link>
        </p>
      </div>
    </div><Preview :data="[]" />
  </div>
</template>

<script>
import articlesData from '../data/articles.json'
import Preview from '@/components/Preview.vue'

export default {
  name: "Home",
  components: { Preview },
  mounted() {
    document.title = '奶糖大人的家 - 小马甲奶糖 - 主页'
  },
  updated() {
    document.title = '奶糖大人的家 - 小马甲奶糖 - 主页'
  },
  data() {
    const preview = []
    const timeMonthMap = new Map() // 统计时间区间

    articlesData.sort((a, b) => {
      return b.time - a.time 
    }).forEach(value => {
      const content = require("../articles/" + value.uuid + ".md")
      const date = (new Date(value.time * 1000))
      const timestr = date.toLocaleString()

      // 统计时间区间
      const timeMonthKey = date.getFullYear() + '-' + (date.getMonth() + 1).toString().padStart(2, '0')
      if (timeMonthMap.get(timeMonthKey)) {
        timeMonthMap.get(timeMonthKey).push(value)
      } else {
        timeMonthMap.set(timeMonthKey, [value])
      }

      preview.push({
        title: value.title,
        uuid: value.uuid,
        time: value.time,
        timestr: timestr,
        data: content
      })
    })

    console.log(preview)
    console.log(timeMonthMap)
    return {
      previews: preview,
      timeart: timeMonthMap
    }
  },
  computed: {
    key() { return this.$route.path + Math.random() }
  }
};
</script>

